<template>
  <div>
    <bread-nav :nav="nav"
               v-if="!isPaperPage"/>

    <div class="card">
      <div class="search">
        <div class="search-item">
          <p class="search-item__title">试题类型</p>
          <el-select v-model="submit.questionType"
                     :disabled="!isAddPage"
                     @change="onQuestionTypeChange">
            <el-option v-for="(item, idx) in $enums.QuestionType.list"
                       :key="idx"
                       :label="item.label"
                       :value="item.value"/>
          </el-select>
        </div>
        <div class="search-item">
          <p class="search-item__title">年级</p>
          <el-select v-model="submit.gradeId"
                     @change="onGradeChange">
            <el-option v-for="(grade, idx) in gradeList"
                       :key="idx"
                       :label="grade.gradeName"
                       :value="grade.id"/>
          </el-select>
        </div>
        <div class="search-item">
          <p class="search-item__title">科目</p>
          <el-select v-model="submit.subjectId"
                     :loading="subjectLoading"
                     @change="onSubjectChange">
            <el-option v-for="(subject, idx) in subjectList"
                       :key="idx"
                       :label="subject.subjectName"
                       :value="subject.id"/>
          </el-select>
        </div>
        <div class="search-item">
          <p class="search-item__title">难度系数</p>
          <el-select v-model="submit.difficultLevel">
            <el-option v-for="(level, idx) in difficultLevelList"
                       :key="idx"
                       :label="level.label"
                       :value="level.value"/>
          </el-select>
        </div>
        <div class="search-item search-item--knowledge">
          <p class="search-item__title">知识点</p>
          <el-select v-model="submit.knowledgeIds"
                     :loading="knowledgeLoading"
                     multiple default-first-option
                     filterable>
            <el-option v-for="(item, idx) in knowledgeList"
                       :key="idx"
                       :label="item.knowledgeName"
                       :value="item.id"/>
          </el-select>
        </div>
      </div>

      <div class="section">
        <h1 class="section__title">题干</h1>
        <wang-editor :content.sync="submit.titleContent"
                     height="200px"
                     v-if="!id || (id && submit.titleContent)"/>
      </div>

      <v-select :submit="submit"
                v-if="submit.questionType === 'SELECT'"/>

      <blank :submit="submit"
             v-if="submit.questionType === 'BLANK'"/>

      <true-or-false :submit="submit"
                     v-if="submit.questionType === 'TRUEORFALSE'"/>

      <answer :submit="submit"
              v-if="submit.questionType === 'ANSWER'"/>

      <words :child-question-list="submit.childQuestionAddList"
             v-if="submit.questionType === 'WORDS'"/>

      <div class="section"
           v-if="submit.questionType === 'COMBINATION'">
        <h1 class="section__title">
          <span>小题</span>
          <span class="section__subtitle">选择录入的题型，并按格式输入题目，保存后将录入题目</span>
        </h1>
        <div class="section__content bg-gray"
             v-if="submit.childQuestionAddList && submit.childQuestionAddList.length">
          <div v-for="(item, idx) in submit.childQuestionAddList"
               :key="idx">
            <h1 class="section__title">题干{{idx + 1}}</h1>
            <wang-editor auto-adjust-tool-bar-visible
                         :content.sync="item.titleContent"/>
            <v-select :submit="item"
                      v-if="item.questionType === 'SELECT'"/>

            <blank :submit="item"
                   v-if="item.questionType === 'BLANK'"/>

            <true-or-false :submit="item"
                           v-if="item.questionType === 'TRUEORFALSE'"/>

            <answer :submit="item"
                    v-if="item.questionType === 'ANSWER'"/>
          </div>
        </div>

        <el-dropdown @command="doAddBigQuestion">
          <el-button class="btn-submit"
                     type="primary">
            添加试题<i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-for="(item, idx) in comboQuestionType"
                              :key="idx"
                              :command="item.value">
              {{item.label}}
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>

      <el-button class="btn-submit"
                 :loading="submitting"
                 @click="doSubmit">
        {{id ? '更新' : '新增'}}
      </el-button>
    </div>
  </div>
</template>

<script>
  import Select from '../../../components/topic/Select'
  import Blank from '../../../components/topic/Blank'
  import TrueOrFalse from '../../../components/topic/TrueOrFalse'
  import Answer from '../../../components/topic/Answer'
  import Words from '../../../components/topic/Words'

  export default {
    components: {
      'v-select': Select,
      Blank,
      TrueOrFalse,
      Answer,
      Words
    },
    data () {
      const vm = this
      return {
        nav: [
          {name: '题库管理', path: 'go(-1)'},
          {name: vm.$route.params.id ? '编辑试题' : '添加试题', path: ''}
        ],
        id: null,
        gradeList: [],
        subjectLoading: false,
        subjectList: [],
        difficultLevelList: [{
          label: '1.0',
          value: 1,
        }, {
          label: '2.0',
          value: 2,
        }, {
          label: '3.0',
          value: 3,
        }, {
          label: '4.0',
          value: 4,
        }, {
          label: '5.0',
          value: 5,
        }, {
          label: '6.0',
          value: 6,
        }],
        knowledgeLoading: false,
        knowledgeList: [],
        trueOrFalse: '正确',
        submit: {
          questionType: '',
          gradeId: null,
          subjectId: null,
          difficultLevel: null,
          knowledgeIds: [],
          titleContent: null,
          answerContent: null,
          questionOptionList: vm.createOptionItem(2),
          childQuestionAddList: []
        },
        submitting: false,
        comboQuestionType: [
          {label: '选择题', value: 'SELECT'},
          {label: '填空题', value: 'BLANK'},
          {label: '作文题', value: 'COMPOSITION'},
          {label: '解答题', value: 'ANSWER'},
          {label: '判断题', value: 'TRUEORFALSE'}
        ]
      }
    },
    props: {
      questionId: {},
      questionType: {}
    },
    computed: {
      isAddPage () {
        return this.$route.name === 'QuestionAdd'
      },
      // 是否是在试卷页面里面
      isPaperPage () {
        return this.$route.name === 'PaperAdd' || this.$route.name === 'PaperEdit'
      },
      isCombinationQuestion () {
        const fields = ['COMBINATION', 'WORDS']
        return fields.indexOf(this.$route.query.questionType) > -1
          || fields.indexOf(this.submit.questionType) > -1
          || fields.indexOf(this.questionType) > -1
      }
    },
    async created () {
      this.id = this.isPaperPage ? this.questionId : this.$route.params.id
      this.queryGradeList()

      if (this.isAddPage) return

      this.$nextTick(async () => {
        let loadingInstance = this.$loading({
          target: '.card',
          text: '试题详情获取中...'
        })
        if (this.isCombinationQuestion) {
          this.submit = await this.$api.getQuestionCombinationDetailInfo({
            id: this.id
          })
        } else {
          let submit = await this.$api.getQuestionDetailInfo({id: this.id})
          if (submit.questionType === 'BLANK') {
            if (submit.answerContent) {
              submit.questionOptionList = submit.answerContent.split(this.$conf.blankQuestionSplitStr).map((item, idx) => {
                return {
                  optionContent: item
                }
              })
            } else submit.questionOptionList = []
          }
          if (!submit.knowledgeIds) submit.knowledgeIds = []
          this.submit = submit
        }
        loadingInstance.close()
        this.querySubjectList()
        this.queryKnowledgeList()
      })
    },
    methods: {
      createOptionItem (length) {
        let arr = []
        for (let i = 0; i < length; i++) {
          arr.push({optionContent: ''})
        }
        return arr
      },
      createQuestionOption (type = this.submit.questionType) {
        switch (type) {
          case 'SELECT':
            return this.createOptionItem(2)
          case 'BLANK':
            return this.createOptionItem(1)
          case 'WORDS':
            return this.createOptionItem(4)
          default:
            return this.createOptionItem(0)
        }
      },
      async queryGradeList () {
        this.gradeList = await this.$api.queryQGradeList({})
      },
      async querySubjectList () {
        this.subjectLoading = true
        this.subjectList = await this.$api.queryQSubjectList({
          gradeId: this.submit.gradeId
        })
        this.subjectLoading = false
      },
      async queryKnowledgeList () {
        this.knowledgeLoading = true
        let knowledgeList = await this.$api.queryQKnowledgeInfoList({
          gradeId: this.submit.gradeId,
          subjectId: this.submit.subjectId
        })
        if (!knowledgeList) knowledgeList = []
        this.knowledgeList = knowledgeList
        this.knowledgeLoading = false
      },
      onQuestionTypeChange (val) {
        // 重置submit.questionOptionAddRequestList的状态
        this.submit.questionOptionList = this.createQuestionOption()

        // 重置submit.childQuestionAddList状态
        this.submit.childQuestionAddList = []

        // // 组合题
        // if (val === 'COMBINATION') {
        //   this.submit.childQuestionAddList = []
        // }

        // 词语填空
        if (val === 'WORDS') {
          this.submit.childQuestionAddList.push({
            questionType: 'SELECT',
            titleContent: null,
            answerContent: null,
            questionOptionList: this.createOptionItem(4)
          })
        }
      },
      onGradeChange (val) {
        this.submit.subjectId = null
        val === null ? this.subjectList = [] : this.querySubjectList()
      },
      onSubjectChange () {
        this.queryKnowledgeList()
        this.submit.knowledgeIds = []
      },
      doAddBigQuestion (val) {
        this.submit.childQuestionAddList.push({
          questionType: val,
          titleContent: null,
          answerContent: null,
          questionOptionList: this.createQuestionOption(val)
        })
      },
      warningMessage (message) {
        return this.$message({
          message,
          type: 'warning'
        })
      },
      async doSubmit () {
        let submit = this.submit
        if (!submit.questionType) {
          this.warningMessage('请先选择试题类型')
          return
        } else if (!submit.gradeId) {
          this.warningMessage('请先选择年级')
          return
        } else if (!submit.subjectId) {
          this.warningMessage('请先选择科目')
          return
        } else if (!submit.difficultLevel) {
          this.warningMessage('请先选择难度')
          return
        } else if (!submit.titleContent) {
          this.warningMessage('请先填写题干')
        }

        if (submit.questionType === 'SELECT') {
          if (!submit.answerContent) {
            this.warningMessage('请先选择一个答案')
            return
          }
          // if (submit.answerContent)
        }

        if (this.submit.questionType === 'BLANK') {
          // TODO 有BUG
          this.submit.answerContent = this.submit.questionOptionList.map(item => item.optionContent).join(this.$conf.blankQuestionSplitStr)
        }
        this.submitting = true
        if (this.isAddPage) {
          // 调用新增接口
          try {
            if (this.isCombinationQuestion) {
              // 新增组合题目
              await this.$api.addCombinationQuestionInfo(this.submit)
            } else {
              // 新增其他题目
              await this.$api.addQuestionInfo(this.submit)
            }
            this.$remind('新增成功')
            this.$router.replace({
              name: 'QuestionList'
            })
          } catch (e) {}
        } else if (this.isPaperPage) {
          // 在组卷页面里
          let api = this.isCombinationQuestion ? this.$api.addCombinationQuestionInfo : this.$api.addQuestionInfo
          let data = null
          try {
            data = await api(this.submit)
          } catch (e) {}
          this.$emit('editQuestion', data)

          this.$emit('editQuestion', data)
        } else {
          // 调用更新接口
          try {
            if (this.isCombinationQuestion) {
              // 更新组合题目
              await this.$api.updateCombinationQuestionInfo(this.submit)
            } else {
              // 更新其他题目
              await this.$api.updateQuestionInfo(this.submit)
            }
            this.$remind('更新成功')
            this.$router.replace({
              name: 'QuestionList'
            })
          } catch (e) {}
        }
        this.submitting = false
      }
    }
  }
</script>

<style lang='sass' scoped>
  .card
    width: 95%
    margin: 20px auto 40px
    padding: 22.5px 15px
    background: #fff
    border-radius: 5px

  .search
    @extend %clearfix
    &-item
      float: left
      width: 135px
      & + &
        margin-left: 20px
      &__title
        color: $main-c
        margin-bottom: 10px
      &--knowledge
        width: 270px
        /deep/ .el-select
          width: 100%

  /deep/ .section
    margin: 25px auto
    &__title
      padding-bottom: 8px
      margin-top: 0
      line-height: 36px
      font-size: $font-large
      color: $main-c
      border-bottom: 1px solid #ddd
    &__content
      margin: 25px 0
      &.bg-gray
        padding: 20px
        background: #f6f8fc
      .section
        margin-top: 25px
        &:first-child
          margin-top: 0
    &__subtitle
      margin-left: 17px
      font-size: $font-normal
      color: #cdcdcd
    &__btn
      float: right
      padding: 0 30px
      margin-left: 15px
      font-size: $font-normal
      background: #f9fafc
      border: 1px solid $main-c
      border-radius: 18px
      cursor: pointer
    &__question
      margin-top: 14px
      .el-checkbox, .el-radio
        width: 5%
        text-align: center
      .wangEditor-container, .el-input
        display: inline-block
        width: 90%
        vertical-align: middle
      .el-icon-delete
        width: 5%
        text-align: center
        font-size: $font-normal + 2px
        color: $error-c
        cursor: pointer

  .btn-submit
    display: block
    margin: 0 auto
    line-height: 1
    color: #fff
    background: $assist-c
    border-color: $assist-c
    &:hover, &:active
      border-color: $assist-c
</style>
